<template>
  <div id = "typeeffectdiv" v-on:click="callFinish()">
    <div class="loading-container" >
      <div><p id="typewords" class="word-text"  v-bind:class="[index%3==1 ? 'saying' : '', 'word-text']">{{word}}</p></div>
    </div>
    <div  style="margin-top:50%" >
      <div><p id="loadingtext" class="progress-text">{{progressText}}</p></div>
    </div>
    <v-touch v-on:swipeleft="onSwipeLeft()">Swipe me!</v-touch>
  </div>
  
</template>


<script>
export default {
  name: "TypingEffect",
  props: {
    msg: String
  },
  data() {
    return {
      words: `距今9000-7500年，来自远古的贾湖部落先民就已经开始酿造葡萄酒。《诗经》所反映的3600年前的殷商时代，就已经有了关于葡萄的文字记载......`,
      word: "111",
      index: 0,
      progressText: "12345",
      percent: 0
    };
  },
  watch: {},
  created() {
    this.setLoadingDisplay();
  },
  methods: {
    setLoadingDisplay() {
      let v1 = setInterval(() => {
        this.word = this.words.substring(0, this.index++);
        if (this.word.length >= this.words-1) {
          clearInterval(v1);
        }
      }, 100);
      let v2 = setInterval(() => {
        this.percent++;
        this.progressText = "loading " + this.percent.toFixed(0) + "%";
        if (this.percent >= 100) {
          clearInterval(v2);
          this.progressText = "载入完成";
        }
      }, 20);
    },
    callFinish() {
      console.log("callFinish");
      if (this.percent >= 100) {
        this.$emit("loadFinish");
        console.log("loadFinish");
      }
    },
    onSwipeLeft(){
      console.log("onSwipeLeft");
    }
  }
};
</script>


<style scoped>
/*光标动画*/
@keyframes caret {
  50% {
    border-color: transparent;
  }
}

/*文本动画：从宽度为0开始*/
@keyframes text {
  from {
    width: 0;
  }
}
.loading-container {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.saying:after {
  content: "|";
  font-family: Arial, sans-serif;
  font-size: 60px;
  line-height: 0;
  display: inline-block;
  vertical-align: baseline;
  opacity: 1;
  animation: caret 200ms infinite;
  word-wrap: break-word;
  word-break: break-all;
}
.word-text {
  position: relative;
  display: inline;
  top: 10%;
  font-size: 60px;
  align-content: center;
}
.progress-text {
  display: inline;
  position: relative;
  top: 70%;
  font-size: 60px;
  align-content: center;
}
</style>
