<template>
  <div class="main-container">
    <div
      class="touch-view"
      @touchcancel="onCancel"
      @touchstart="onStart"
      @touchend="onEnd"
      @touchmove="onMove"
    >
      <img :src="tipsrc" v-if="showTips" class="tipsimg" />
    </div>
  </div>
</template>


<script>
import image_tap from "@/assets/img/tap.png";
import image_swipeup from "@/assets/img/swipeup.png";
import image_swipedown from "@/assets/img/swipedown.png";
import image_shake from "@/assets/img/shake.png";
import image_like from "@/assets/img/like.png";
import image_dislike from "@/assets/img/dislike.png";
import { setTimeout } from "timers";
import Shake from 'shake.js'
const MODE_TAP = 0;
const MODE_UP = 1;
const MODE_DOWN = 2;
const MODE_SHAKE = 3;
const MODE_LIKE = 4;
export default {
  name: "OperatorView",
  props: {
    tipsrc: String,
    mode: Number,
    videoId: Number
  },
  data() {
    return {
      showTips: true,
      startX: 0,
      startY: 0,
      modes: [
        {
          mode: MODE_TAP,
          src: image_tap
        },
        {
          mode: MODE_UP,
          src: image_swipeup
        },
        {
          mode: MODE_DOWN,
          src: image_swipedown
        },
        {
          mode: MODE_SHAKE,
          src: image_shake
        },
        {
          mode: MODE_LIKE,
          src: image_like
        }
      ]
    };
  },
  watch: {},
  mounted(){
    // 实例化一个 shake 对象
    let myShakeEvent = new Shake({
      threshold: 20, // 默认摇动阈值
      timeout: 1200 // 默认两次事件间隔时间
    })
    // 监听设备的动作
    myShakeEvent.start();
    // 添加一个事件监听
    window.addEventListener('shake', this.shakeEventDidOccur, false);
  },
  created() {},
  methods: {
    onMove(event) {
      event.preventDefault();
      let y = event.touches[0].clientY;
      if (y + 100 < this.startY && this.mode == MODE_UP) {
        console.log("swipeup");
        this.$emit("swipeup");
      }
      if (y - 100 > this.startY && this.mode == MODE_DOWN) {
        console.log("swipedown");
        this.$emit("swipedown");
      }
      return false;
    },
    onCancel(event) {
      this.startX = 0;
      this.startY = 0;
    },
    onStart(event) {
      if (this.mode == MODE_TAP) {
        console.log("tap");
        this.$emit("tap");
      }
      this.startX = event.touches[0].clientX;
      this.startY = event.touches[0].clientY;
      if (MODE_TAP != this.mode) {
        let vdo = document.getElementById("videoPlay" + this.videoId);
        if(vdo.paused){
          vdo.play();
          vdo.pause();
        }
      }
    },
    onEnd(event) {
      this.startX = 0;
      this.startY = 0;
      console.log("onEnd" + this.mode);
    },
    getDistance(x1, y1, x2, y2) {
      let x = x1 - x2;
      let y = y1 - y2;
      return Math.sqrt(x * x + y * y);
    },
    // 摇动的回调函数
    shakeEventDidOccur () {
      if (this.mode == MODE_SHAKE) {
        console.log("shake");
        this.$emit("shake");
      }
      alert('shake')
      if (window.navigator.vibrate) {
        navigator.vibrate(500)
      }
      
    }
  }
};
</script>

<style scoped>
.main-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tipsimg {
  position: absolute;
  display: block;
  align-items: center;
  width: 200px;
  height: 200px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  opacity: 1;
  z-index: 6;
}
.touch-view {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1920px;
  opacity: 1;
  z-index: 7;
  /* background-color: green; */
  justify-content: center;
}
</style>
