<template>
  <div class="main-container">
    <div
      v-for="item in items"
      v-bind:key="item.id"
      v-bind:class="{'video-container':item.isShow ,'video-hidden-container':!item.isShow }"
    >
      <video
        :id="forId(item.id)"
        v-bind:src="item.src"
        playsinline
        x5-playsinline
        webkit-playsinline="true"
        x-webkit-airplay="true"
        x5-video-player-type="h5"
        x5-video-can-play-with-audio="true"
        v-bind:class="{'video-container':item.isShow ,'video-hidden-container':!item.isShow }"
        x5-video-player-fullscreen="false"
        x5-video-orientation="portrait"
        preload="auto"
        @ended="onVideoEnded(item.id)"
      ></video>
    </div>
    <!-- <img :src="tipsrc" v-if="showTips" class="tipsimg"  @touchmove="onMove"/> -->
    <OperatorView
      v-bind:mode="mode"
      v-bind:videoId="currentId"
      v-bind:tipsrc="tipsrc"
      v-on:swipeup="swipeup(currentId)"
      v-on:swipedown="swipedown(currentId)"
      v-on:tap="tap(currentId)"
      v-on:shake="shake(currentId)"
    />
  </div>
</template>


<script>
import video001 from "@/assets/video/video001.mp4";
import video002 from "@/assets/video/video002.mp4";
import video003 from "@/assets/video/video003.mp4";
import video004 from "@/assets/video/video004.mp4";
import image_tap from "@/assets/img/tap.png";
import image_swipeup from "@/assets/img/swipeup.png";
import image_swipedown from "@/assets/img/swipedown.png";
import image_shake from "@/assets/img/shake.png";
import image_like from "@/assets/img/like.png";
import image_dislike from "@/assets/img/dislike.png";
import { setTimeout } from "timers";
import OperatorView from "@/components/OperatorView.vue";
// v-bind:style="{ display:item.isShow?'flex':'none',zIndex:item.isShow?5:4 }"
// v-on:click="playVideo(item.id)"
const MODE_TAP = 0;
const MODE_UP = 1;
const MODE_DOWN = 2;
const MODE_SHAKE = 3;
const MODE_LIKE = 4;
export default {
  name: "VideoPlayer",
  props: {},
  components: {
    OperatorView
  },
  data() {
    return {
      mode: 0,
      swipesuccess: false,
      tipsrc: image_tap,
      currentId: 0,
      showTips: true,
      items: [
        {
          id: 0,
          src: video001,
          isShow: true,
          mode: MODE_TAP,
          tipsrc: image_tap
        },
        {
          id: 1,
          src: video002,
          isShow: false,
          mode: MODE_UP,
          tipsrc: image_swipeup
        },
        {
          id: 2,
          src: video003,
          isShow: false,
          mode: MODE_DOWN,
          tipsrc: image_swipedown
        },
        {
          id: 3,
          src: video003,
          isShow: false,
          mode: MODE_SHAKE,
          tipsrc: image_shake
        }
      ]
    };
  },
  watch: {},
  created() {
    
  },
  methods: {
    playVideo(id) {
      this.showTips = false;
      let vdo = document.getElementById("videoPlay" + id);
      vdo.play();
      console.log(id + "play");
    },
    forId: function(index) {
      return "videoPlay" + index;
    },
    onVideoEnded(id) {
      this.items[id].isShow = false;
      this.showTips = true;
      this.swipesuccess = false;
      // setTimeout(()=>{this.items[id].isShow = false;},1000)
      if (id + 1 < this.items.length) {
        this.items[id + 1].isShow = true;
        this.mode = this.items[id + 1].mode;
        this.tipsrc = this.items[id + 1].tipsrc;
        this.currentId = this.items[id + 1].id;
        // this.playVideo(id+1);
      } else {
        this.items[0].isShow = true;
        this.mode = this.items[0].mode;
        this.tipsrc = this.items[0].tipsrc;
        this.currentId = this.items[0].id;
        // this.playVideo(0);
      }
      console.log(id + "onended");
    },
    swipeup(id) {
      if (this.mode != MODE_UP) {
        return;
      }
      console.log("swipeup" + this.mode);
      this.swipesuccess = true;
      this.playVideo(id);
    },
    swipedown(id) {
      if (this.mode != MODE_DOWN) {
        return;
      }
      this.swipesuccess = true;
      console.log("swipedown" + this.mode);
      this.playVideo(id);
    },
    tap(id) {
      if (this.mode != MODE_TAP) {
        return;
      }
      console.log("tap" + this.mode);
      this.playVideo(id);
    },
    shake(id) {
      if (this.mode != MODE_SHAKE) {
        return;
      }
      console.log("shake" + this.mode);
      this.playVideo(id);
    },
    onMove(event) {
      
      event.preventDefault();
      if (this.swipesuccess) {
        // this.playVideo(id);
        console.log("onMove" + this.mode);
      }
    }
  }
};
</script>

<style scoped>
@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fadeout /*Safari and Chrome*/ {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.main-container {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: center;
}
.video-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1920px;
  opacity: 1;
  z-index: 6;
  justify-content: center;
}
.video-hidden-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1920px;
  opacity: 1;
  z-index: 5;
  justify-content: center;
}
.tipsimg {
  position: absolute;
  display: block;
  align-items: center;
  width: 200px;
  height: 200px;
  opacity: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 6;
}
.touch-view {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1920px;
  opacity: 0.5;
  z-index: 7;
  /* background-color: green; */
  justify-content: center;
}
</style>
